<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 单位编码 -->
            <a-form-model-item
              label="单位编码"
              prop="unitCode"
              ref="unitCode"
            >
              <a-input
                v-model="entity.unitCode"
                placeholder="请输入单位编码，格式为UM开头加4位数字"
                name="unitCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 单位名称 -->
            <a-form-model-item
              label="单位名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入单位名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 单位符号 -->
            <a-form-model-item
              label="单位符号"
              prop="symbol"
              ref="symbol"
            >
              <a-input
                v-model="entity.symbol"
                placeholder="请输入单位符号"
                name="symbol"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 单位类型 -->
            <a-form-model-item
              label="单位类型"
              prop="unitType"
              ref="unitType"
            >
              <a-select
                v-model="entity.unitType"
                placeholder="请选择单位类型"
                name="unitType"
              >
                <a-select-option value="长度单位">长度单位</a-select-option>
                <a-select-option value="重量单位">重量单位</a-select-option>
                <a-select-option value="体积单位">体积单位</a-select-option>
                <a-select-option value="面积单位">面积单位</a-select-option>
                <a-select-option value="温度单位">温度单位</a-select-option>
                <a-select-option value="时间单位">时间单位</a-select-option>
                <a-select-option value="电力单位">电力单位</a-select-option>
                <a-select-option value="其他单位">其他单位</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 基本单位 -->
            <a-form-model-item
              label="基本单位"
              prop="baseUnit"
              ref="baseUnit"
            >
              <a-switch v-model="entity.baseUnit" @change="handleBaseUnitChange" />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 转换比率 -->
            <a-form-model-item
              label="转换比率"
              prop="conversionRate"
              ref="conversionRate"
            >
              <a-input-number
                v-model="entity.conversionRate"
                :disabled="entity.baseUnit"
                :min="0.000001"
                :precision="6"
                style="width: 100%"
                placeholder="请输入转换比率"
                name="conversionRate"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status" name="status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 标准信息 -->
            <a-form-model-item
              label="标准信息"
              prop="standardInfo"
              ref="standardInfo"
            >
              <a-select
                v-model="entity.standardInfo"
                placeholder="请选择标准信息"
                name="standardInfo"
              >
                <a-select-option value="国际标准单位(SI)">国际标准单位(SI)</a-select-option>
                <a-select-option value="非SI单位">非SI单位</a-select-option>
                <a-select-option value="行业标准单位">行业标准单位</a-select-option>
                <a-select-option value="自定义单位">自定义单位</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 单位描述 -->
            <a-form-model-item 
              label="单位描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述"
                :rows="2"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 单位使用示例 -->
      <g-collapse headerName="单位使用示例">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-example-btn" type="primary" icon="plus" @click="addExample">添加示例</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="exampleColumns" 
                :dataSource="entity.examples" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="usageValue" slot-scope="text, record, index">
                  <a-input v-model="entity.examples[index].usageValue" placeholder="请输入示例值" />
                </template>
                <template slot="usageContext" slot-scope="text, record, index">
                  <a-input v-model="entity.examples[index].usageContext" placeholder="请输入使用场景" />
                </template>
                <template slot="note" slot-scope="text, record, index">
                  <a-input v-model="entity.examples[index].note" placeholder="请输入说明" />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeExample(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditUnitFoMeasure",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        unitCode: "",
        name: "",
        symbol: "",
        unitType: "",
        baseUnit: false,
        conversionRate: 1,
        standardInfo: "",
        description: "",
        examples: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        unitCode: [
          { required: true, message: "请输入单位编码", trigger: "change" },
          { pattern: /^UM\d{4}$/, message: "单位编码必须为UM开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入单位名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "单位名称不能包含前后空格", trigger: "change" }
        ],
        symbol: [
          { required: true, message: "请输入单位符号", trigger: "change" }
        ],
        unitType: [
          { required: true, message: "请选择单位类型", trigger: "change" }
        ],
        conversionRate: [
          { required: true, message: "请输入转换比率", trigger: "change" },
          { type: "number", min: 0.000001, message: "转换比率必须大于0", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ],
        standardInfo: [
          { required: true, message: "请选择标准信息", trigger: "change" }
        ]
      },
      exampleColumns: [
        {
          title: "示例值",
          dataIndex: "usageValue",
          key: "usageValue",
          width: "25%",
          scopedSlots: { customRender: "usageValue" }
        },
        {
          title: "使用场景",
          dataIndex: "usageContext",
          key: "usageContext",
          width: "30%",
          scopedSlots: { customRender: "usageContext" }
        },
        {
          title: "说明",
          dataIndex: "note",
          key: "note",
          width: "35%",
          scopedSlots: { customRender: "note" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取单位数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        unitCode: "",
        name: "",
        symbol: "",
        unitType: "",
        baseUnit: false,
        conversionRate: 1,
        standardInfo: "",
        description: "",
        examples: [],
        status: "启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        this.entity = {
          ...this.entity,
          ...row,
          // 确保examples数组被正确初始化
          examples: row.examples || []
        };
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        // 检查基本单位与转换率
        if (this.entity.baseUnit && this.entity.conversionRate !== 1) {
          this.$message.error("基本单位的转换比率必须为1");
          return;
        }

        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 处理基本单位变更
    handleBaseUnitChange(checked) {
      if (checked) {
        this.entity.conversionRate = 1;
      }
    },
    
    // 添加使用示例
    addExample() {
      this.entity.examples.push({
        id: Date.now(), // 使用时间戳作为临时ID
        usageValue: "",
        usageContext: "",
        note: ""
      });
    },
    
    // 删除使用示例
    removeExample(index) {
      this.entity.examples.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-example-btn {
  margin-bottom: 16px;
}
</style> 